.filter {
  display: flex;
  align-items: center;

  &__item {
    &:first-of-type {
      margin-right: 20px;
    }

    a {
      font-size: 16px;
      font-family: "MuseoSansCyrl-700", sans-serif;
      color: rgb(0, 0, 0);

      padding-bottom: 6px;

      @include mq("tablet", max) {
        width: 100%;
        display: block;
        text-align: center;

        border-bottom: 1px solid $my-gray;
        padding-top: 10px;
        padding-bottom: 10px;
      }

      &.active {
        color: $my-green;
        border-bottom: 1px solid $my-green;
      }

      @include mq("phone-small", max) {
        font-size: 14px;
      }
    }

    @include mq("tablet", max) {
      width: 50%;
      margin: 0 !important;
    }
  }
}

.catalogBtn {
  background-color: #f8f8f8;
  padding: 15px;
  margin-top: 10px;
  margin-bottom: 10px;

  @include mq("tablet-wide", max) {
    margin: 0;
  }

  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;

  &__item {
    margin: 5px 25px 5px 0;

    @include mq("tablet", max) {
      margin: 0px 15px 5px 0;

      // &:not(:last-of-type) {
      //   flex-grow: 1;
      // }
    }

    &.active {
      a {
        color: $my-green;
        border-bottom-color: transparent;
      }
    }
  }
}

.news {
  &__img {
    position: relative;
    text-align: center;

    // a > img,
    // & > img {
    //   width: 100%;
    //   height: auto;
    // }
  }

  &__icon {
    position: absolute;
    top: 10px;
    right: 10px;

    img {
      max-width: 100%;
      height: auto;
    }
  }

  &__category {
    font-size: 16px;
    font-family: "MuseoSansCyrl-500", sans-serif;
    color: rgb(6, 193, 41);

    span {
      &:not(:first-of-type) {
        margin-left: 10px;
        position: relative;

        &::after {
          content: "/";
          position: absolute;
          left: -10px;
          @extend %v-c;
          color: $my-gray;
          font-size: 14px;
          font-weight: 700;
        }
      }
    }
  }

  &__category--title {
    font-size: 14px;
    font-family: "MuseoSansCyrl-500", sans-serif;
    color: rgb(157, 157, 157);
  }

  &__date,
  &__time,
  &__viewed {
    font-size: 14px;
    font-family: "MuseoSansCyrl-500", sans-serif;
    color: rgb(117, 111, 111);
  }

  &__title--big {
    font-size: 20px;
    font-family: "MuseoSansCyrl-700", sans-serif;
    color: rgb(0, 0, 0);
    line-height: 1.3;

    @include mq("desktop", max) {
      font-size: 16px;
    }
  }

  &__title--small {
    font-size: 16px;
    font-family: "MuseoSansCyrl-700", sans-serif;
    color: rgb(0, 0, 0);
    line-height: 1.3;

    @include mq("tablet-wide", max) {
      font-size: 14px;
    }

    @include mq("tablet", max) {
      font-size: 16px;
    }
  }

  &__subtitle {
    margin-top: 10px;
    font-size: 14px;
    font-family: "MuseoSansCyrl-500", sans-serif;
    color: rgb(0, 0, 0);
    line-height: 1.5;

    @include text-clamp(9, auto);

    @include mq("desktop", max) {
      @include text-clamp(7, auto);
    }

    @include mq("tablet-wide", max) {
      @include text-clamp(3, auto);
      font-size: 12px;
    }

    @include mq("tablet", max) {
      @include text-clamp(5, auto);
      font-size: 14px;
    }
  }

  &.news__bottom {
    position: relative;

    .news {
      &__subtitle {
        @include text-clamp(3, auto);
      }

      @include mq("tablet", max) {
        &__img {
          display: none;
        }

        &__subtitle {
          display: none;
        }
      }
    }

    .news__img {
      a {
        display: block;
        position: relative;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          // z-index: -1;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: repeating-linear-gradient(
            180deg,
            rgba($my-dark, 0) 0%,
            rgba($my-dark, 0) 50%,
            rgba($my-dark, 0.85) 100%
          );
        }

        text-align: center;
      }

      // img {
      //   max-width: 100%;
      //   height: 275px;

      //   @include mq("tablet-wide", max) {
      //     height: unset;
      //     max-height: 275px;
      //   }
      // }
    }

    .news__content .news__viewed {
      display: block;

      @include mq("tablet", max) {
        display: none;
      }
    }

    & > .news__viewed {
      display: block;
      margin-bottom: 10px;

      @include mq("tablet", max) {
        display: none;
      }
    }

    & > .news__dates {
      @include mq("tablet-wide", min) {
        display: flex;
        align-items: center;
      }

      @include mq("tablet-wide", max) {
        display: block;
      }

      @include mq("tablet", max) {
        display: none;
      }
    }
  }

  &.news__top {
    position: relative;

    .news {
      &__content {
        @include mq("tablet", max) {
          border-bottom: 1px solid rgba(#000, 0.2);
          padding-bottom: 20px;
        }
      }
    }

    .news__img {
      a {
        display: block;
        position: relative;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          // z-index: -1;
          right: 0;
          bottom: 0;
          width: 100%;
          height: 100%;
          background-image: repeating-linear-gradient(
            180deg,
            rgba($my-dark, 0) 0%,
            rgba($my-dark, 0) 50%,
            rgba($my-dark, 0.85) 100%
          );
        }
        img {
          max-width: 100%;
          max-height: 400px;
        }
      }
    }

    .news__content .news__viewed {
      display: block;

      @include mq("tablet", max) {
        display: none;
      }
    }

    & > .news__viewed {
      display: none;

      @include mq("tablet", max) {
        display: block;
        margin-bottom: 10px;
      }
    }

    .news__dates {
      display: flex;
      align-items: center;
    }
  }

  &__dates {
    position: absolute;
    bottom: 0;
    left: 15px;

    .news__date,
    .news__time,
    .news__category--title {
      color: white;
    }

    &--two {
      display: none;

      @include mq("tablet", max) {
        display: flex;
      }
    }
  }

  &__item {
    .news {
      &__subtitle {
        -webkit-line-clamp: unset;
        height: auto;
        display: block;
        -webkit-box-orient: vertical;
        overflow: unset;
      }
    }
  }

  &__external-link {
    margin-left: 10px;

    i {
      position: relative;

      &::after {
        content: "";
        width: 35px;
        height: 35px;
        background-color: $my-gray;
        @extend %c-c;
        border-radius: 50%;
        z-index: -1;

        @extend %tr03;
      }
    }

    span {
      margin-left: 15px;
      text-decoration: underline;
    }

    a {
      font-size: 14px;
      font-family: "MuseoSansCyrl-500", sans-serif;
      color: rgb(0, 0, 0);
      line-height: 1.5;

      &:hover {
        color: $my-green;

        i {
          &::after {
            background-color: $my-dark-2;
          }
        }
      }
    }
  }
}

.news__sliders {
  // padding: 5px;

  @include mq("phablet", max) {
    // padding: 10px;
  }

  .news__slider {
    position: relative;

    & > .news__dates {
      display: flex;
      align-items: center;
    }
  }

  .news {
    &__subtitle {
      @include text-clamp(4, auto);
      color: white;
    }

    &__title--small {
      color: white;
    }
  }

  .news__img {
    a {
      display: block;
      position: relative;

      text-align: center;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        // z-index: -1;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background-image: repeating-linear-gradient(
          180deg,
          rgba($my-dark, 0) 0%,
          rgba($my-dark, 0) 50%,
          rgba($my-dark, 0.85) 100%
        );
      }

      // img {
      //   max-width: 100%;
      //   height: 275px;
      // }
    }
  }
}

.share {
  &--title {
    font-size: 16px;
    font-family: "MuseoSansCyrl-700", sans-serif;
    color: rgb(0, 0, 0);
    line-height: 1.313;
  }

  &--links {
    a {
      margin-right: 25px;

      i {
        position: relative;
        color: white;
        transition: transform 0.1s linear;

        &::after {
          content: "";
          width: 35px;
          height: 35px;
          background-color: $my-gray;
          @extend %c-c;
          border-radius: 50%;
          z-index: -1;

          @extend %tr03;
        }

        &.fa-vk {
          &::after {
            background-color: #4d76a1;
          }
        }

        &.fa-telegram-plane {
          &::after {
            background-color: #039be5;
          }
        }

        &.fa-odnoklassniki {
          &::after {
            background-color: #ffae00;
          }
        }

        &.fa-facebook-f {
          &::after {
            background-color: #3b5998;
          }
        }

        &.fa-whatsapp {
          &::after {
            background-color: #10a745;
          }
        }

        &.fa-twitter {
          &::after {
            background-color: #55acee;
          }
        }

        &.fa-instagram {
          &::after {
            // background-color: #4d76a1;
            background: linear-gradient(
              45deg,
              #f09433 0%,
              #e6683c 25%,
              #dc2743 50%,
              #cc2366 75%,
              #bc1888 100%
            );
          }
        }
      }

      &:hover {
        i {
          transform: translateY(-5px);
        }
      }
    }
  }
}

.rek {
  width: 100%;
  height: auto;
  padding: 10px 0;

  &__big {
    img {
      height: 100px;
      width: 100%;
    }
  }

  &__vertical {
    img {
      max-width: 100%;
      height: auto;
    }
  }
}

.column-12 {
  @include mq("tablet", max) {
    padding: 0;
  }
}

.top_selled {
  padding: 5px;

  @include mq("phablet", max) {
    padding: 10px;
  }

  &__img {
    background-color: #fff;
    padding: 10px;

    display: flex;
    justify-content: center;
    align-items: center;

    border-style: solid;
    border-width: 1px;
    border-color: rgba(34, 34, 34, 0.2);

    height: 200px;

    @include mq("phablet", max) {
      height: 220px;
    }

    img {
      max-width: 100%;
      height: auto;
    }

    position: relative;
  }
}

