.contact {
  position: absolute;
  top: 50px;
  left: 0;
  height: 400px;
  margin: 0 15px;
  border: 1px solid rgba($color: $my-dark, $alpha: 0.2);

  @include mq("tablet", max) {
    position: unset;
    margin: 0;
    border: none;
    padding-left: 0;
    margin-right: 0;
  }

  background-color: #fff;

  padding: 40px;
  &__title {
    font-size: 20px;
    font-family: "MuseoSansCyrl-900", sans-serif;
    color: rgb(26, 26, 24);
    line-height: 1.2;
    text-align: left;
    margin-bottom: 30px;
  }

  &__item {
    font-size: 18px;
    color: rgb(34, 34, 34);
    line-height: 1.444;
    text-align: left;
    margin-bottom: 20px;

    p {
      font-family: "MuseoSansCyrl-700", sans-serif;
      font-size: 18px;
      margin-bottom: 0;
    }

    div {
      font-family: "MuseoSansCyrl-500", sans-serif;
      font-size: 16px;
    }
  }
}

.my-map {
  position: absolute;
  height: 500px;
  width: 100%;

  @include mq("tablet", max) {
    position: unset;
  }
}

.my-container-fluid {
  position: relative;
  height: 500px;
  background-color: #fff;

  @include mq("tablet", max) {
    position: unset;
    height: auto;
  }
}

.feedback {
  &__title {
    font-size: 20px;
    font-family: "MuseoSansCyrl-900", sans-serif;
    color: rgb(26, 26, 24);
    line-height: 1.2;
    text-align: left;
  }

  form {
    margin-top: 20px;

    input {
      border-style: solid;
      display: block;
      width: 100%;
      border-width: 1px;
      border-color: rgba(0, 0, 0, 0.2);
      border-radius: 2px;
      background-color: rgb(248, 248, 248);
      height: 45px;
      padding: 5px 15px;

      @extend %tr03;
      &:focus {
        border-color: rgba(0, 0, 0, 0.6);
      }
    }

    textarea {
      border-style: solid;
      display: block;
      width: 100%;
      border-width: 1px;
      border-color: rgba(0, 0, 0, 0.2);
      border-radius: 2px;
      background-color: rgb(248, 248, 248);
      padding: 10px 15px 5px;
      height: 167px;
      resize: none;

      @extend %tr03;
      &:focus {
        border-color: rgba(0, 0, 0, 0.6);
      }
    }

    .my-btn-send {
      width: 200px;
      height: 45px;
      padding: 5px 15px;
      font-size: 18px;
      font-family: "MuseoSansCyrl-700", sans-serif;
      color: rgb(255, 255, 255);
      line-height: 1.333;
      text-align: center;
      background-color: $my-green;
      border: 1px solid $my-green;
      border-radius: 3px;      
    }
  }
}
