.navbar {
    top: 0;
    z-index: 9999;
    padding: 15px 0;
    @include mq('tablet-wide', max) {
        min-height: 65px;
    }
    
    .container {
        padding-left: 15px;
        padding-right: 15px;
    }

    &-brand {
        @include mq('tablet-wide', max) {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
        }
    }

    &-toggler {
        padding: 0;
        width: 30px;

        transform: scale(.75);
        @include mq('tablet-wide', max) {
            right: 10px;
            top: 50%;
            transform: translateY(-50%) scale(.75);
            position: absolute;
        }
        @include mq('tablet', max) {
            right: 10px;
        }
    }

    &-toggler &-toggler-icon,
    &-toggler &-toggler-icon::after,
    &-toggler &-toggler-icon::before {
        height: 2px;
    }

    .collapse {
        .navbar-nav .nav-item {
            font-size: 16px;
            font-family: "MuseoSansCyrl-500", sans-serif;
            padding-top: 0;
            padding-bottom: 0;

            a {
                padding-top: 0;
                padding-bottom: 0;
            }

            &:hover {
                a {
                    padding-top: 0;
                    padding-bottom: 0;
                    color: $my-green;
                }
            }


        }

        .navbar-nav .nav-item.active>.nav-link,
        .navbar-nav .nav-item:active:focus>.nav-link,
        .navbar-nav .nav-item:active>.nav-link,
        .navbar-nav .nav-item:focus>.nav-link,
        .navbar-nav .nav-item:hover>.nav-link {
            color: $my-green;
        }

        @media (min-width: 1023px) {
            .navbar-nav .nav-link {
                padding-right: 10px;
                padding-left: 10px;
            }
        }
    }
}

.bsnav-mobile {
    z-index: 2222;
    .navbar {
        background-color: $my-dark-2;
        padding: 72px 0 15px;

        &-nav {
            padding: 0;
        }

        .nav {
            &-item {
                padding: 0px 15px 0px 25px;

                &:first-of-type {
                    padding: 15px 15px 15px 25px;
                    background-color: $my-dark;

                    a {
                        color: white;
                        margin-right: 10px;
                        text-transform: uppercase;

                        font-size: 14px;
                        font-family: "MuseoSansCyrl-500", sans-serif;

                        &.active {
                            color: rgb(6, 193, 41);
                        }
                    }
                }
            }

            &-link:active,
            &-link:focus {
                color: $my-green !important;
            }
        }
    }

    .navbar-nav .nav-item.active>.nav-link,
    .navbar-nav .nav-item:active:focus>.nav-link,
    .navbar-nav .nav-item:active>.nav-link,
    .navbar-nav .nav-item:focus>.nav-link,
    .navbar-nav .nav-item:hover>.nav-link {
        color: $my-green;
    }

    .navbar-expand-lg .navbar-nav .nav-link,
    .navbar-nav .nav-link {
        color: white;

        &:hover {
            color: $my-green;
        }
    }

    // .navbar-nav .nav-item:hover>.nav-link {
    //     color: #fff;
    // }
}

.sticked {
    box-shadow: 0 0.1875rem 0.3125rem 0 rgba(44, 46, 48, 0.1);
}

