.my-table {
    img {
        max-width: 40px;
    }

    thead {
        th {
            font-size: 16px;
            font-family: "MuseoSansCyrl-700", sans-serif;
            color: rgb(161, 154, 154);
        }
    }

    .table {



        &-date {
            div {

                &:nth-of-type(1) {
                    font-size: 16px;
                    font-family: "MuseoSansCyrl-700", sans-serif;
                    color: rgb(0, 0, 0);
                    line-height: 1.313;
                }

                &:nth-of-type(2) {
                    font-size: 14px;
                    font-family: "MuseoSansCyrl-500", sans-serif;
                    color: rgb(0, 0, 0);
                    line-height: 1.313;
                }
            }
        }

        &-temp {
            &>div {
                &:nth-of-type(1) {
                    font-size: 16px;
                    font-family: "MuseoSansCyrl-700", sans-serif;
                    color: rgb(0, 0, 0);
                    line-height: 1.313;
                }

                &:nth-of-type(2) {
                    font-size: 14px;
                    font-family: "MuseoSansCyrl-500", sans-serif;
                    color: rgba(0, 0, 0, .7);
                    line-height: 1.313;
                }
            }
        }

        &-title {
            font-size: 16px;
            font-family: "MuseoSansCyrl-500", sans-serif;
            color: rgb(0, 0, 0);
            line-height: 1.5;
        }

        &-wet {
            font-size: 16px;
            font-family: "MuseoSansCyrl-500", sans-serif;
            color: rgb(0, 0, 0);
            line-height: 1.5;
        }
    }
}