.contact__item p {
  font-family: "MuseoSansCyrl-700", sans-serif;
  font-size: 18px;
  margin-bottom: 0;
}

.contact__item div {
  font-family: "MuseoSansCyrl-500", sans-serif;
  font-size: 16px;
}

.my-map {
  position: absolute;
  height: 500px;
  width: 100%;
}

@media only screen and (max-width: 767px) {
  .my-map {
    position: unset;
  }
}

.my-container-fluid {
  position: relative;
  height: 500px;
  background-color: #fff;
}

@media only screen and (max-width: 767px) {
  .my-container-fluid {
    position: unset;
    height: auto;
  }
}

.feedback__title {
  font-size: 20px;
  font-family: "MuseoSansCyrl-900", sans-serif;
  color: #1a1a18;
  line-height: 1.2;
  text-align: left;
}

.feedback form {
  margin-top: 20px;
}

.feedback form input {
  border-style: solid;
  display: block;
  width: 100%;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  background-color: #f8f8f8;
  height: 45px;
  padding: 5px 15px;
}

.feedback form input:focus {
  border-color: rgba(0, 0, 0, 0.6);
}

.feedback form textarea {
  border-style: solid;
  display: block;
  width: 100%;
  border-width: 1px;
  border-color: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
  background-color: #f8f8f8;
  padding: 10px 15px 5px;
  height: 167px;
  resize: none;
}

.feedback form textarea:focus {
  border-color: rgba(0, 0, 0, 0.6);
}

.feedback form .my-btn-send {
  width: 200px;
  height: 45px;
  padding: 5px 15px;
  font-size: 18px;
  font-family: "MuseoSansCyrl-700", sans-serif;
  color: white;
  line-height: 1.333;
  text-align: center;
  background-color: #06c129;
  border: 1px solid #06c129;
  border-radius: 3px;
}

.swiper-container {
  padding: 60px 0 0;
  margin-top: -60px;
}

.swiper-button-prev {
  right: 40px;
  left: unset;
}

.swiper-button-next {
  right: 0;
}

.swiper-button-prev, .swiper-button-next {
  top: 20px;
}

.swiper-button-prev::after, .swiper-button-next::after {
  font-size: 18px;
  color: white;
}

#myBtn {
  display: none;
  position: fixed;
  bottom: 20px;
  right: 15px;
  z-index: 99;
  font-size: 18px;
  border: none;
  outline: none;
  background-color: #06c129;
  color: white;
  cursor: pointer;
  padding: 15px;
  border-radius: 4px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
}

#myBtn:hover {
  background-color: #555;
}

.my-table img {
  max-width: 40px;
}

.my-table thead th {
  font-size: 16px;
  font-family: "MuseoSansCyrl-700", sans-serif;
  color: #a19a9a;
}

.my-table .table-date div:nth-of-type(1) {
  font-size: 16px;
  font-family: "MuseoSansCyrl-700", sans-serif;
  color: black;
  line-height: 1.313;
}

.my-table .table-date div:nth-of-type(2) {
  font-size: 14px;
  font-family: "MuseoSansCyrl-500", sans-serif;
  color: black;
  line-height: 1.313;
}

.my-table .table-temp > div:nth-of-type(1) {
  font-size: 16px;
  font-family: "MuseoSansCyrl-700", sans-serif;
  color: black;
  line-height: 1.313;
}

.my-table .table-temp > div:nth-of-type(2) {
  font-size: 14px;
  font-family: "MuseoSansCyrl-500", sans-serif;
  color: rgba(0, 0, 0, 0.7);
  line-height: 1.313;
}

.my-table .table-title {
  font-size: 16px;
  font-family: "MuseoSansCyrl-500", sans-serif;
  color: black;
  line-height: 1.5;
}

.my-table .table-wet {
  font-size: 16px;
  font-family: "MuseoSansCyrl-500", sans-serif;
  color: black;
  line-height: 1.5;
}
