.section-filter {
    @include mq('tablet', max) {
        padding-top: 20px;
    }
}

.section-news {
    padding-top: 40px;
    padding-bottom: 10px;

    @include mq('tablet', max) {
        padding-top: 20px;
    }
}

.rek-container {
    @include mq('tablet', max) {
        padding: 0;
    }
}

.section-category {
    padding-top: 20px;
    padding-bottom: 10px;

    .news.news__bottom .news__subtitle {
        display: -webkit-box !important;
    }

    @include mq('tablet-wide', max) {
        .news__dates {
            display: block !important;
        }
    }
    @include mq('tablet', max) {
        padding-top: 10px;

        .news__dates {
            display: flex !important;
        }
    }


    .category-row {
        &>div:first-of-type {
            @include mq('tablet', max) {
                .news {
                    &__img {
                        display: block;
                        margin-bottom: 10px;
                    }

                    &__subtitle {
                        display: block;
                        @include text-clamp(4, auto);
                    }
                }
            }
        }
    }
}

.section-news-item {
    padding-bottom: 10px;

    @include mq('tablet', max) {
        padding-top: 0px;
        margin-top: -20px;
    }


    .news__bottom {
        .news__title--small+div {
            &>div {
                @include mq('tablet-wide', max) {
                    font-size: 12px;
                }

                @include mq('tablet', max) {
                    font-size: 14px;
                }
            }
        }
    }
}

.section-popular_news {
    padding: 30px 0;
    background-color: #3b3b3b;
}