.modal {
    z-index: 99999;
    width: 100%;
    margin: 0;
    background-color: rgba($color: $my-dark, $alpha: .75);
    padding-right: 0 !important;

    @include mq('tablet-wide', min) {
        background-color: rgba($color: $my-dark, $alpha: 0);
    }

    &-backdrop.show {
        @include mq('tablet-wide', min) {
            // opacity: 0;
        }
    }
    
    &-dialog {
        width: 100%;
        max-width: 100%;
        margin: 0;
        padding: 0;
        animation: unset !important;
        transform: unset !important;
        transition: .1s all linear !important;

        @include mq('tablet-wide', min) {
            max-width: 680px;
            margin: 0 auto;
        }
        @include mq('desktop', min) {
            max-width: 850px;
            margin: 0 auto;
        }
        @include mq('desktop-wide', min) {
            max-width: 850px;
            margin: 0 auto;
        }
    }
    
    &-content {
        height: 65px;
        @include mq('tablet-wide', min) {
            height: 70px;
        }
        position: relative;
        border: unset;
        border-radius: 0;
    }

    &-body, &-header {
        margin: 0;
        padding: 0;
        border: unset;
        border-radius: 0;
    }

    .close {
        position: absolute;
        right: 0px;
        top: 50%;
        @include mq('tablet-wide', min) {
            height: 100%;
        }
        transform: translateY(-50%);
        z-index: 99;
        margin: 0;
        background-color: #fff;
        opacity: 1;
    }
}