.footer {
    &__links {
        background-color: $my-dark-2;
        padding: 30px 0;

        .container {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;

            @include mq('tablet', max) {
                flex-wrap: wrap;
            }
        }

        ul {
            margin: 0;
            padding: 0;
            list-style-type: none;

            li {
                a {
                    color: white;
                    font-size: 14px;
                    font-family: "MuseoSansCyrl-500", sans-serif;
                    line-height: 1.75;

                    &:hover {
                        color: $my-green;
                    }

                    i {
                        font-size: 24px;
                    }
                }
            }

            @include mq('tablet', max) {
                margin-top: 15px;
                width: 48%;

                &:last-of-type {
                    width: 100%;
                    display: flex;
                    justify-content: center;
                }

                li {
                    a {
                        line-height: 1;
                    }
                }
            }
        }
    }

    &__bottom {
        background-color: $my-dark;
        padding: 15px 0;

        .copyright {
            font-size: 14px;
            font-family: "MuseoSansCyrl-300", sans-serif;
            color: rgb(255, 255, 255);
            line-height: 1.75;
            text-align: center;

            a {
                &:hover {
                    color: $my-green;
                }
            }

            @include mq('tablet', max) {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
            }
        }
    }
}