// Texts
.text-green {
    color: $my-green;
}

.bg-green {
    background-color: $my-green;
    border-color: $my-green;

    &:hover, &.active, &:focus {
        background-color: rgba($color: $my-green, $alpha: .85);
        border-color: rgba($color: $my-green, $alpha: .85);
    }

    padding: 8px 35px;
}

.bg-color-green {
    background-color: $my-green;
}

.height-100 {
    @include mq('tablet', max) {
        height: 100%;
    }
}