.swiper {
    &-container {
        padding: 60px 0 0;
        margin-top: -60px;
    }
    &-button {
        &-prev {
            right: 40px;
            left: unset;
        }

        &-next {
            right: 0;
        }

        &-prev,
        &-next {
            top: 20px;
            &::after {
                font-size: 18px;
                color: white;
            }
        }
    }
}
