.form-search {
    width: 100%;
    position: relative;

    input {
        width: 100%;
        height: 65px;
        @include mq('tablet-wide', min) {
            height: 70px;
        }
        border: unset;
        border-bottom: 1px solid transparent;
        font-size: 16px;
        background-color: white;
        // background-image: url('../img/searchicon.png');
        // background-position: 10px 22.5px;
        // background-repeat: no-repeat;
        padding: 12px 20px 12px 40px;
        color: $my-dark;
        transition: width 0.4s ease-in-out;

        cursor: pointer;

        &::placeholder {
            color: $my-dark;
        }

        @include mq('tablet', max) {
            left: 100%;
        }
    }

    input:focus {
        cursor: auto;
    }

    .my-btn--search {
        margin: 0;
        padding: 0;
        background-color: transparent;
        border: unset;
        position: absolute;
        z-index: 999;

        i {
            color: $my-dark;
        }

        position: absolute;
        left: 15px;
        top: 50%;
        transform: translateY(-50%);
    }
}