.lang {
    a {
        font-size: 14px;
        font-family: "MuseoSansCyrl-500", sans-serif;
        margin-left: 10px;
        padding: 5px 4px 3px;
        line-height: 1;
        text-transform: uppercase;

        &.active {
            color: rgb(6, 193, 41);
            border-style: solid;
            border-width: 1px;
            border-color: rgb(6, 193, 41);
            border-radius: 3px;
        }

        &:hover {
            color: $my-green;
        }
    }
}

.breadcrumb {
    background-color: transparent;
    padding-left: 0;
    margin-bottom: 0;

    &-item {
        font-size: 14px;
        font-family: "MuseoSansCyrl-500", sans-serif;
        color: rgb(0, 0, 0);

        &.active {
            color: $my-green;
        }
    }
}

.pagination {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10px;

    .page {
        &-item {
            margin: 0 6px;

            &:first-of-type,
            &:last-of-type {
                .page-link {
                    border: unset;
                    border-radius: 0;
                }
            }

            &.active,
            &.disabled {
                .page-link {
                    border-color: $my-green;
                    color: white;
                    background-color: $my-green;
                    width: 40px;
                    height: 40px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }

        &-link {
            border-radius: 50%;
            font-size: 14px;
            font-family: "MuseoSansCyrl-500", sans-serif;
            color: rgb(0, 0, 0);
            text-align: center;
            border-color: $my-green;
        }
    }
}

.date {
    color: rgba(16, 16, 16, 0.8);
    flex-basis: 30%;
}

.currency {
    color: rgba(16, 16, 16, 0.8);
    flex-basis: 30%;
    text-align: center;
}

.weather {
    position: relative;
    color: rgba(16, 16, 16, 0.8);
    flex-basis: 30%;
    text-align: center;

    &__content {
        position: absolute;
        right: 0;
        @extend %v-c;

        display: flex;
        justify-content: flex-end;
        align-items: center;
        height: 100%;
        width: 100%;

        @include mq("tablet-wide", max) {
            // flex-direction: column;
            flex-wrap: wrap;
            margin-top: -10px;
            // align-items: flex-start;
        }

        @include mq("tablet", max) {
            margin-top: -4px;
        }
    }

    &__icon {
        i {
            font-size: 30px;

            @include mq("tablet", max) {
                font-size: 20px;
            }

            @include mq("phone-small", max) {
                font-size: 18px;
            }
        }
    }

    &__count {
        margin-left: 10px;
        margin-right: 10px;
        min-width: 20px;
        font-size: 24px;

        @include mq("tablet-wide", max) {
            font-size: 18px;
            margin-right: 0;
            margin-left: 5px;
        }

        @include mq("tablet", max) {
            font-size: 18px;
        }

        @include mq("phone-small", max) {
            font-size: 16px;
        }

        sup {
            i {
                transform: translateY(-3px);
                font-size: 8px;

                @include mq("tablet", max) {
                    font-size: 5px;
                }

                @include mq("phone-small", max) {
                    font-size: 4px;
                }
            }
        }
    }

    &__region {
        font-size: 14px;

        @include mq("tablet-wide", max) {
            width: 100%;
            text-align: right;
        }

        @include mq("tablet", max) {
            // text-align: center;
        }

        @include mq("tablet", max) {
            font-size: 12px;
            margin-top: -5px;
        }

        @include mq("phone-small", max) {
            font-size: 10px;
        }
    }
}

.searching {
    margin-right: 25px;

    @include mq("tablet-wide", max) {
        position: absolute;
        left: 15px;
    }
}

.banner {
    margin-top: 65px;

    @include mq("tablet-wide", min) {
        margin-top: 72px;
    }

    background-color: $my-white;
    font-family: "MuseoSansCyrl-500", sans-serif;
    padding: 15px 0;
    font-size: 14px;

    @include mq("tablet", max) {
        font-size: 12px;
    }

    @include mq("phone-small", max) {
        font-size: 10px;
    }

    .container {
        display: flex;
        justify-content: space-between;
    }
}

.currency-table {
    thead {
        tr {
            th {
                font-size: 16px;
                font-family: "MuseoSansCyrl-700", sans-serif;
                color: rgb(255, 255, 255);
            }
        }
    }

    tbody {
        tr {
            th {
                font-size: 16px;
                font-family: "MuseoSansCyrl-500", sans-serif;
                color: rgb(0, 0, 0);

                font-size: 14px;
            }
        }
    }

    tbody tr:nth-of-type(odd) {
        background-color: #fafafa;
    }
}

.my-border {
    border-bottom: 1px solid rgba(#000, 0.2);
    padding: 5px 0 15px;
    @include mq("tablet-wide", min) {
        border-bottom: unset;
        padding: 0;
    }

    @include mq("tablet-wide", max) {
        display: none;
    }

    @include mq("tablet", max) {
        display: flex;
    }
}
